body {
  margin: 0;
  background-color: #F6EBCD;
  font-family: sans-serif;
}

.container {
  max-width: 900px;
  margin: auto;
  padding: 30px;
}

/* Profile Section */
.profile-section {
  display: flex;
  align-items: center;
  gap: 40px;
}

.profile-image {
  width: 120px;
  height: 120px;
  background: #e43f1a;
  border-radius: 50%;
}

.name {
  margin: 0;
  font-size: 24px;
}

.intro {
  margin-top: 5px;
  color: #333;
}

/* Skills */
.skills {
  display: flex;
  gap: 15px;
  margin-top: 20px;
}

.skill-icon {
  width: 30px;
  height: 30px;
  background: #7a0000;
  border-radius: 50%;
}

/* Divider */
.divider {
  margin: 30px 0;
  border: none;
  border-top: 1px solid #000;
}

/* Projects */
.project-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.project-card {
  height: 240px;   /* 전체 카드 높이는 고정 */
  overflow: hidden; /* 내부만 줄어들도록 */
  text-align: center;
}

.thumbnail {
  width: 100%;
  height: 180px;
  background: rgb(63, 0, 0);
}

.project-title {
  margin-top: 10px;
}

.main-card .thumbnail {
  width: 100%;
  height: 220px;
  background: #e43f1a;
  font-weight: bold;
}

/* 기본 썸네일 크기 */
.project-card .thumbnail {
  width: 100%;
  height: 240px;
  background: #e43f1a;
  transition: height 0.25s ease;
}

/* 마우스가 전체 그리드 위에 있을 때: 전체 카드 축소 */
.project-grid:hover .project-card .thumbnail {
  height: 150px;
  background: #6a0000;
}

/* 하지만 hover된 카드만 다시 원래 크기로 복귀 */
.project-grid .project-card:hover .thumbnail {
  height: 240px !important;
  background: #e43f1a;
}

